import React, { Component } from 'react'

export default class Todoinput extends Component {
    constructor(){
        super();

    }
    handleInput = (e)=>{
        if(e.keyCode === 13){
            this.props.addTodo(e.target.value);
            e.target.value = ''
        }
    }
   
    render() {
        return (
            <div className="header">
          <div className="header-content">
              <div className="header-title">ToDoList</div>
              <input type="text" style={{ marginTop:20,width:500,borderRadius: 4}}  
               onKeyDown={(e)=>this.handleInput(e)} placeholder="添加待办事项" required="required" ></input>
          </div>
      </div>
        )
    }
}