import React, { Component } from 'react'
export default class Todoing extends Component {
    
    render() {
        return (
            <div className="List">
                <h1 className="list-title">正在进行中
                <span className="title-numb">{this.props.todo.length}</span></h1> 
                <ul className="list">
                    {
                        this.props.todo.map((item,idx)=>
                        <li key={idx}><input type='checkbox' key={idx} checked={false} 
                        onChange={()=>{this.props.checkeBox(idx)}}></input><p>{item}</p>
                        <span onClick={()=>{this.props.delTodo(idx)}}>-</span></li>)
                    }
                </ul>
            </div>
        )
    }
}
